.content{
    display: flex;
    justify-content: center;
    .blocks{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 1000px;
        
        .btnTel{
            border-radius: 12px;
            border: 1px solid gray;
            height: 26px;
            width: 100px;
        }
        .title{
            margin-top: 3%;
            align-self: flex-start;
            font-size: 25px;
            font-weight: bold;
        }

        .description{
            margin-top: 3%;
            font-size: 18px;
            align-self: flex-start;
            word-wrap: break-word;
            line-height: 1.5rem;
        }

        .line{
            height: 1px;
            width: 550px;
            align-self: flex-start;
            margin-top: 2%;
            margin-bottom: 2%;
            background-color: gray;
        }

        .lineLight{
            height: 1px;
            width: 550px;
            align-self: flex-start;
            margin-top: 2.2%;
            margin-bottom: 2.2%;
            opacity: 30%;
            background-color: rgb(57, 54, 54);
        }

        .information{
            align-self: flex-start;
            width: 550px;
            display: flex;
            flex-direction: column;
            margin-bottom: 5%;
            .informLine{
                width: 550px;
                height: 30px;
                font-size: 16px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                .informLeft{
                    color: gray;
                }
                
            }
            
        }
        
    }

    // div:nth-child(1){
    //         width: 300px;
    //     height: 300px;
    // }
}

.images{
    height: 400px;
    background-color: #272727;
    width: 100%;
    display: flex;
    justify-content: center;


    div {
        width: 700px;
        height: 400px;
        img {
            height: 400px;
            background-position: center center;
            object-fit: contain;
        }
    }
}
.map {
    align-self: flex-start;
    margin-top: 5%;
    height: 460px;
    width: 900px;

}